































import auth from '@/store/modules/auth';
import { Component, Vue } from 'vue-property-decorator';

@Component
export default class Navbar extends Vue {

  public get isLogin(): boolean {
    const userTmp = auth.getUser;
    if (userTmp.login !== '') {
      return true;
    }
    return false;
  }
  /**
   * logOut
   */
  public logOut(): void {
    auth.logOut();
    // @ts-ignore.
    this.$router.go('/');
  }
}
